:root {
    --blue: #23B5D3;
    --green: #9EE943;
    --slate: #DAF7DC;
    --gray: #6C6EA0;
    --gray-light:#8485ae;
    --black: #101419;
}

*{
    margin:0;
    padding:0;
}

h1{
    font-family:'Restroking';
    line-height: 0.75;
    margin: 0 0 0 3rem;
    font-size: 95px;
    color: var(--blue);
}

h1:hover{
    color:var(--gray-light)
}

@media only screen and (max-width: 1000px) {
    h1{
        font-size:4rem;
        margin:0 0 0 0;
        width:100vw;
        
    }
    .base{
        margin:0 0 0 0;
        justify-content: center;
        align-content: center;
    }
    .nav-container{
        margin: 0 0 0 0;
        align-self: center;
        align-content: center;
        align-items: center;
        justify-content: center;
        justify-items: center;
        justify-self: center;
        width: 100vw;
    }
    h3{
        font-size: .75rem;
    }
}

h3{
    font-family: 'RockSalt';
    font-size: small;
    
}

.base {
    display:flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items:center;
    background-color: var(--black);
    margin: right 100px;
    /* min-height: 100vh; */

}
.nav-container {
    min-height: 4rem;
    /* width:950px; */
    justify-content:center;
    align-items:center;
    display:flex;
    flex-direction: column;
    flex-wrap:wrap;
    margin: 0 0 0 0;

}

.nav-container .hexagonArea{
    display:flex;

}


.nav-container .hexagonArea.last{
    transform:translateY(-20px)
}

.nav-container .hexagonArea .hexagon{
    position:relative;
    width:100px;
    height: 115px;
    margin: 0 6px;
    clip-path:polygon(0 25%,50% 0,100% 25%,100% 75%,50% 100%,0 75%);
    background-color: var(--blue);
    display:flex;
    justify-content: center;
    align-items:center;
    flex-direction: column;
    transition: background-color 320ms ease;
}
.nav-container .hexagonArea  .hexagon:hover{
    background-color: var(--gray-light);
    filter: contrast(1);
    cursor:pointer;
}



.selected{
    background-color: var(--gray) !important;
}